* {
  font-family: sans-serif;
  font-weight: lighter;
}
body {
  background-color: #eee;
}

.navButtons button {
  cursor: pointer;
  font-size: 20px;
  margin-right: 5px;
  border: none;
}
.navButtons .link {
  background-color: rgba(0,0,0,0.3);
}
.navButtons .link a {
  text-decoration: none;
  color: white;
}
.navButtons .current {
  background-color: rgba(0,0,0,0.5);
  border-bottom: 4px solid #444;
}

.navBar {
  font-size: 20px;
  color: cornflowerblue;
  font-weight: bold;
}
.right-nav {
  float: right;
}

.note {
  position: relative;
  left: 0px;
  right: 0px;
  font-size: 20px;
  background-color: #666;
  margin: 8px;
  border-radius: 2px;
  padding: 5px;
  color: #eee;
  height: 30px;
  line-height: 30px;
}
.note.active {
  border: 4px solid brown;
  opacity: 0.35;
}
.note .container-btn {
  position: absolute;
  top: 5px;
  right: 5px;
}
.note .btn {
  color: brown;
  font-size: 25px;
  background-color: #888;
  height: 30px;
  padding-left: 6px;
  padding-top: 0px;
  border-radius: 2px;
  border: none;
}
.note .btn:hover {
  background-color: #aaa;
  cursor: pointer;
}
.note .remove-btn {
  float: right;
}
.note .update-btn {
  position: absolute;
  right: 40px;
  color: #bbb;
}

.form * {
  font-size: 25px;
  margin: 3px;
  padding: 3px 8px 3px 8px;
}
.form {
  margin: 10px;
}
.form input {
  width: 500px;
}
.form .cancel-btn {
  line-height: 32px;
}

.notes {
  max-width: 700px;
}
.heading {
  text-align: center;
  color: #444;
}

